* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  li {
    list-style: none;
    text-decoration: none;
  }
  a {
    list-style: none;
    color: inherit;
    text-decoration: none;
  }
  
  body {
    line-height: 1;
    color: #333;
    
  }

  .header {
    height: 100px;
  }

  .header .inner {
    max-width: 1280px;
    width: calc(100% - 30px);
    height: 100%;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
  }

  .nav .gnb {
    display: flex;
    gap: 30px;
  }

  .nav_open {
    width: 20px;
    position: relative;
    z-index: 999;
  }
  .nav_open img {
    width: 100%;

    
  }
  
  .all_nav {
    width: 50%;
    height: 100vh;
    position: fixed;
    right: -100%;
    top: 0;
    background-color: #e0cccc;
    
  }

  .all_nav.on {
    right: 0;
    transition: 0.7s;
  }
 